import './styles.scss';
import React, {Component} from 'react';
import {connect} from 'react-redux';
import bem from 'bem-cn';

const b = bem('progress');

class Header extends Component {
    calculateProgress = () => {
        console.log('progress');
        const {flow} = this.props;
        let percentage = 0;

        if(flow.currentStepIndex === 0) {
            return 0;
        }

        if(flow.currentStepIndex === flow.flows[flow.currentFlow].steps.length - 1) {
            return 100;
        }

        percentage = flow.flows[flow.currentFlow] && (100 / flow.flows[flow.currentFlow].steps.length) * flow.currentStepIndex;

        return percentage;
    }

    render() {
        return(
            <div className={b()}>
                <div className={b('fake-bar')}/>
                <div className={b('bar-wrapper')}>
                    <div className={b('bar')} style={{width: this.calculateProgress() + '%'}}/>
                </div>
            </div>
        )
    }
}

const mapState = state => ({flow: state});

export default connect(mapState, null)(Header);



