import './styles.scss';
import React, {Component} from 'react';
import { connect } from 'react-redux';
import { prevStep, jumpTo } from '../../actions';
import bem from 'bem-cn';

import logo from './logo.svg';

const b = bem('header');

class Header extends Component {
    prevStep = () => {
        this.props.prevStep()
    }

    goToStart = () => {
        this.props.jumpTo('start');
    }

    getBreadcrump = () => {
        let prevStepValue = '';
        const { currentFlow, currentStepIndex, flows, breadcrump } = this.props.state;

        const prevStep = currentFlow ? flows[currentFlow].steps[currentStepIndex-1] : '';

        if(currentFlow && prevStep && breadcrump[currentFlow] && breadcrump[currentFlow][prevStep]) {
            prevStepValue = currentFlow && prevStep ? breadcrump[currentFlow][prevStep].value : '';
        }

        return prevStepValue;
    }

    render() {
        console.log("header", this.props.state);
        return(
            <div className={b()}>
                <img className={b('logo')} src={logo} onClick={this.goToStart}/>
                <span className={b('breadcrump')}>{this.getBreadcrump()}</span>
                <span className={b('back-button')} onClick={this.prevStep}/>
            </div>
        )
    }
}

const mapState = state => {
    return {
        state
    }
}

export default connect(mapState, {prevStep, jumpTo})(Header);



