import './styles.scss';
import React, {Component} from 'react';
import {connect} from 'react-redux';
import { nextStep } from '../../../actions'
import bem from 'bem-cn';
import Step from '../../../components/step';
import SelectionFilter from '../../../components/selection-filter';
import insuranceTypes from '../../../data/insuranceTypes';
const b = bem('kasko-insurance-selection-step');
import logo from './logo.svg';

class KaskoInsuranceSelectionStep extends Component {
    nextStep = () => {
        const { nextStep } = this.props;
        nextStep();
    }

    render() {
        return (
        <div className={b()}>
            <Step title="Wähle deine Schadenfreiheitsklasse für die KFZ-Volkasko-Versicherung">

                <div className={b('logo')}>
                    <img src={logo}/>
                    <span className={b('label')}>Volkasko</span>
                </div>

                <SelectionFilter list={insuranceTypes} currentStepId="kasko-insurance-selection"/>
            </Step>
        </div>
        )
    }
}

export default connect(null, { nextStep })(KaskoInsuranceSelectionStep);



