import './styles.scss';
import React, {Component} from 'react';
import {connect} from 'react-redux';
import { nextStep } from '../../../actions'
import bem from 'bem-cn';
import Step from '../../../components/step';
import SelectionFilter from '../../../components/selection-filter';
import cars from '../../../data/cars';
const b = bem('car-selection-step');

class CarSelectionStep extends Component {

    render() {
        return (
            <Step title="Von welcher Marke ist dein Fahrzeug?">
                <SelectionFilter list={cars} currentStepId="car-selection" />
            </Step>
        )
    }
}

export default connect(null, { nextStep })(CarSelectionStep);



