import './styles.scss';
import React, {Component} from 'react';
import bem from 'bem-cn';
import Step from '../../step';
import icon4 from './icon4.svg';
import icon5 from './icon5.svg';
import IconBox from '../../option-box';
const b = bem('step2');

class SubOptions1 extends Component {

    render() {
        return (
        <Step title="Wähle eine der folgenden Optionen:">
            <div className={b('boxes')}>
                <IconBox
                    iconUrl={icon4}
                    text="Fahrzeug aus der Liste wählen"
                />
                <IconBox
                    iconUrl={icon5}
                    text="Daten vom Fahrzeugschein eingeben"
                    switchFlow
                />
            </div>
        </Step>
        )
    }
}

export default SubOptions1;



