import './styles.scss';
import React, {Component} from 'react';
import {connect} from 'react-redux';
import { nextStep } from '../../../actions'
import bem from 'bem-cn';
import Step from '../../../components/step';
import SelectionFilter from '../../../components/selection-filter';
import insuranceTypes from '../../../data/insuranceTypes';
const b = bem('insurance-selection-step');
import logo from './logo.svg';

class InsuranceSelectionStep extends Component {
    render() {
        return (
        <div className={b()}>
            <Step title="Wähle deine Schadenfreiheitsklasse für die KFZ-Haftpflicht-Versicherung">
                <div className={b('logo')}>
                    <img src={logo}/>
                    <span className={b('label')}>Haftpflicht</span>
                </div>
                <SelectionFilter list={insuranceTypes} currentStepId="insurance-selection"/>
            </Step>
        </div>
        )
    }
}

export default connect(null, { nextStep })(InsuranceSelectionStep);



