import './styles.scss';
import React, {Component} from 'react';
import bem from 'bem-cn';
import Step from '../../step';
import icon1 from './icon1.svg';
import icon2 from './icon2.svg';
import icon3 from './icon3.svg';
import IconBox from '../../option-box';
const b = bem('options');

class Options extends Component {
    render() {
        return (
        <Step title="Wähle eine der folgenden Optionen:">
            <div className={b('boxes')}>
                <IconBox
                    iconUrl={icon1}
                    text="Ich möchte meine Versicherung wechseln"
                    flow="1"
                />
                <IconBox
                    iconUrl={icon2}
                    text="Dies ist meine erste  KFZ-Versicherung"
                    flow="2"
                />
                <IconBox
                    iconUrl={icon3}
                    text="Ich möchte ein anderes Auto versichern"
                    flow="3"
                />
            </div>
        </Step>
        )
    }
}

export default Options;



