import './styles.scss';
import React, {Component} from 'react';
import bem from 'bem-cn';
import Step from '../../step';
import icon1 from './icon1.svg';
import icon2 from './icon2.svg';
import IconBox from '../../option-box';
const b = bem('step1b');

class Step1b extends Component {
    render() {
        return (
        <Step title="Wähle eine der folgenden Optionen:">
            <div className={b('boxes')}>
                <IconBox
                    iconUrl={icon1}
                    text="Ich möchte meine Versicherung wechseln"
                />
                <IconBox
                    iconUrl={icon2}
                    text="Dies ist meine erste  KFZ-Versicherung"
                />
            </div>
        </Step>
        )
    }
}

export default Step1b;



