import './styles.scss';
import React, {Component} from 'react';
import {connect} from 'react-redux';
import InputSlider from 'react-input-slider';
import Button from '../../button';
import { nextStep, jumpTo } from '../../../actions';
import bem from 'bem-cn';
const b = bem('offer-final-step');


class OfferFinalStep extends Component {
    state = {
        x: 3000,
        insurance: 'KASKO',
        plan: undefined
    }
    nextStep = () => {
        const { nextStep } = this.props;
        nextStep();
    }

    editData = step => {
        const { jumpTo, flow } = this.props;
        const stepIndex = flow.flows[flow.currentFlow].steps.indexOf(step);
        console.log('EDIT', step, stepIndex);
        jumpTo(stepIndex);
    }

    handleChange = (pos) => {
        this.setState({
            x: pos.x
        });
    }

    render() {
        const {insurance, plan} = this.state;
        const { flow } = this.props;
        const data = flow.breadcrump[flow.currentFlow];
        return (
            <div className={b()}>
                <div className={b('left-column')}>
                    <h2 className={b('title')}>Überblick</h2>
                    { data && <div className={b('data-selection')}>
                        <div className={b('data-set')}>
                            <div className={b('data-set-title-wrapper')}>
                                <span className={b('data-set-title')}>Dein Fahrzeug</span>
                                <span className={b('edit-data')} onClick={() => this.editData('sub-options-1')}/>
                            </div>
                            <div className={b('data-set-group')}>
                                <span className={b('data-set-label')}>Fahrzeug:</span>
                                <span className={b('data-set-value')}>{data['car-engine-selection'] && data['car-engine-selection'].value }</span>
                            </div>
                            <div className={b('data-set-group')}>
                                <span className={b('data-set-label')}>Jahr der Erstzulassung:</span>
                                <span className={b('data-set-value')}>{data['car-registration-date-input'] && data['car-registration-date-input'].value }</span>
                            </div>
                        </div>

                        <div className={b('data-set')}>
                            <div className={b('data-set-title-wrapper')}>
                                <span className={b('data-set-title')}>Persönliche Angaben</span>
                                <span className={b('edit-data')} onClick={() => this.editData('age-input-step')}/>
                            </div>
                            <div className={b('data-set-group')}>
                                <span className={b('data-set-label')}>Dein Alter:</span>
                                <span className={b('data-set-value')}>{data['age-input-step'] && data['age-input-step'].value }</span>
                            </div>
                            <div className={b('data-set-group')}>
                                <span className={b('data-set-label')}>Fahrer Alterspanne:</span>
                                <span className={b('data-set-value')}>{data['additional-drivers-age-selection'] && data['additional-drivers-age-selection'].value }</span>
                            </div>
                            <div className={b('data-set-group')}>
                                <span className={b('data-set-label')}>PLZ:</span>
                                <span className={b('data-set-value')}>{data['postal-code-input'] && data['postal-code-input'].value }</span>
                            </div>
                        </div>

                        {data['insurance-selection'] && <div className={b('data-set')}>
                            <div className={b('data-set-title-wrapper')}>
                                <span className={b('data-set-title')}>Vorherige Versicherung</span>
                                <span className={b('edit-data')} onClick={() => this.editData('insurance-selection')}/>
                            </div>
                            <div className={b('data-set-group')}>
                                <span className={b('data-set-label')}>Schadenfreiheitsklasse Haftpflicht:</span>
                                <span className={b('data-set-value')}>{data['insurance-selection'] && data['insurance-selection'].value }</span>
                            </div>
                            <div className={b('data-set-group')}>
                                <span className={b('data-set-label')}>Schadenfreiheitsklasse Volkasko:</span>
                                <span className={b('data-set-value')}>{data['kasko-insurance-selection'] && data['kasko-insurance-selection'].value }</span>
                            </div>
                        </div>}

                    </div>}
                </div>
                <div className={b('right-column')}>
                    <h2 className={b('title')}>Welches Versicherungspaket möchtest du haben?</h2>
                    <div className={b('insurance-options')}>
                        <div className={b('radio-group')}>
                            <span className={b('radio')} onClick={() => this.setState({insurance: 'TEILKASKO'})}>
                                <span className={b('radio-circle').mix(insurance === 'TEILKASKO' ? 'checked' : '')}/>
                                <span className={b('radio-label')}>Teilkasko</span>
                            </span>
                            <span className={b('radio-info-icon')}/>
                        </div>
                        <div className={b('radio-group')}>
                            <span className={b('radio')} onClick={() => this.setState({insurance: 'KASKO'})}>
                                <span className={b('radio-circle').mix(insurance === 'KASKO' ? 'checked' : '')}/>
                                <span className={b('radio-label')}>Volkasko</span>
                            </span>
                            <span className={b('radio-info-icon')}/>
                        </div>
                    </div>

                    <div className={b('plans')}>
                        <span className={b('plan')}>
                            <span className={b('plan-title')}>Base Premium</span>
                            <span className={b('plan-button').mix(plan === 'BASE_PREMIUM' ? 'checked' : '')} onClick={() => this.setState({plan: 'BASE_PREMIUM'})}>20 Euro/month</span>
                        </span>
                        <span className={b('plan')}>
                            <span className={b('plan-title')}>Premium/km</span>
                            <span className={b('plan-button').mix(plan === 'PREMIUM' ? 'checked' : '')} onClick={() => this.setState({plan: 'PREMIUM'})}>2 ct Euro/km</span>
                        </span>
                    </div>

                    <div className={b('calculation')}>
                        <h2 className={b('calculation-title')}>Calculation</h2>
                        <span className={b('range-value')}>Bis zu {Math.floor(this.state.x)} km</span>
                        <span className={b('calculation-note')}>Bewege das Auto um die Kilometeranzahl zu verändernm</span>

                        <div className={b('input-slider')}>
                            <InputSlider
                                className="slider slider-x"
                                axis='x'
                                x={this.state.x}
                                xmin={3000}
                                xmax={10000}
                                onChange={this.handleChange}
                            />
                        </div>
                    </div>
                </div>
                <div className={b('final-offer')}>
                    <span className={b('offer-msg')}>Your estimated pay will be <span className={b('offer-value')}>25 Euro/month</span></span>
                    <Button text="Senden"/>
                </div>
            </div>
        )
    }
}

const mapState = state => ({flow: state});

export default connect(mapState, { nextStep, jumpTo })(OfferFinalStep);



