import './styles.scss';
import React, {Component} from 'react';
import {connect} from 'react-redux';
import { nextStep, setSelection, setBreadcrump } from '../../../actions'
import bem from 'bem-cn';
import Step from '../../../components/step';
import Button from '../../../components/button';
const b = bem('additional-drivers-age-selection-step');

class AdditioanalDriversAgeSelectionStep extends Component {
    state = {}
    nextStep = () => {
        const { nextStep, setSelection, setBreadcrump, flow } = this.props;
        const { currentFlow } = flow;
        nextStep();
        setSelection({
            'additional-drivers-age-selection': {
                startAge: this.startAge.value,
                endAge: this.endAge.value
            }
        })

        const bredcrumpValue = this.startAge.value && this.endAge.value
            ? this.startAge.value + ' - ' + this.endAge.value + ' Jahre alt'
            : '';

        setBreadcrump({
            [currentFlow]: {
                'additional-drivers-age-selection': {
                    value: bredcrumpValue
                }
            }
        })
    };

    componentWillMount() {
        const { selections } = this.props.flow;
        console.log('this', this.state);
        this.setState({...selections['additional-drivers-age-selection']});
    }

    render() {
        const {startAge, endAge } = this.state;
        return (
            <Step title="Alter der zusätzlichen Fahrer:">
                <div className={b('container')}>
                    <div className={b('inputs')}>
                        <div className={b('input')}>
                            <input type="number"
                                   min="17"
                                   placeholder="17"
                                   ref={input => this.startAge = input}
                                   defaultValue={startAge}
                            />
                        </div>
                        <span>-</span>
                        <div className={b('input')}>
                            <input type="number"
                                   min="17"
                                   max="85"
                                   placeholder="25"
                                   ref={input => this.endAge = input}
                                   defaultValue={endAge}
                            />
                        </div>
                        <span className={b('label')}>Jahre</span>
                    </div>
                    <Button text="Weiter" center onClick={this.nextStep}/>
                </div>
            </Step>
        )
    }
}

const mapState = state => ({flow: state});

export default connect(mapState, { nextStep, setSelection, setBreadcrump })(AdditioanalDriversAgeSelectionStep);



